@import url(https://fonts.googleapis.com/css2?family=Baloo+Paaji+2:wght@300;400;500;600;700&display=swap);
.Home {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	align-content: flex-start;
}

#Page {
	background-color: #005e83;
	color: black;
	flex: 1 1;
}

.instaPost {
	display: flex;
	justify-content: center;
}

.history-item {
	display: flex;
	flex: 0 1 100px;
	flex-direction: column;
	background-color: rgb(0, 38, 53);
	padding: 10px;
	margin: 10px;
	margin-top: 20px;
	border-radius: 10px;
	flex: 1 1;
	max-width: 550px;
}

.history-item-title {
	display: flex;
	justify-content: center;
	font-weight: 700;
}

.history-item-youtube {
	align-self: center;
}

.history-item-caption {
	font-weight: 300;
}

.About {
	flex-direction: column;
	text-align: center;
}

.bio {
	margin: 0px 10px;
	padding-bottom: 15px;
	text-align: left;
}

.profile-picture {
	width: 300px;
	float: left;
	margin: 1px 10px;
}

#Page {
	background-color: #005e83;
	color: black;
	flex: 1 1;
}

.NavigationBar {
	display: flex;
	justify-content: space-around;
	height: 42px;
}

.NavOptions {
	background: rgb(0, 52, 73);
	min-width: 125px;
	width: 50%;
	height: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
	border-top-left-radius: 20px;
	border-top-right-radius: 20px;
	margin: 1px;
	font-weight: 400;
}

.NavOptionsSelected {
	background-color: #005e83;
	width: 125px;
	height: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
	border-top-left-radius: 20px;
	border-top-right-radius: 20px;
	margin: 1px;
	font-weight: 600;
}

.NavLinks {
	text-decoration: none;
	cursor: pointer;
}

@media (max-width: 620px) {
	.NavOptions {
		background: rgb(0, 52, 73);
		min-width: 100px;
		width: 33vw;
		height: 100%;
		display: flex;
		justify-content: center;
		align-items: center;
		border-top-left-radius: 20px;
		border-top-right-radius: 20px;
		margin: 1px;
	}

	.NavOptionsSelected {
		background-color: #005e83;
		width: 33vw;
		height: 100%;
		display: flex;
		justify-content: center;
		align-items: center;
		border-top-left-radius: 20px;
		border-top-right-radius: 20px;
		margin: 1px;
	}
}

.Header {
	display: flex;
	justify-content: space-between;
	margin: 0;
	padding: 0;
}

.Title {
	margin-left: 30px;
	font-weight: 700;
}

@media (max-width: 620px) {
	.Header {
		display: flex;
		flex-direction: column;
	}

	.Title {
		display: flex;
		justify-content: space-around;
	}
}

.App {
	display: flex;
	flex-direction: column;
	justify-items: stretch;
	height: 100vh;
}

body {
	background-color: rgb(0, 38, 53);
}

* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	color: #f5f5f5;
	font-family: "Baloo Paaji 2";
	font-weight: 500;
}

